// Fonts
@import url("https://use.typekit.net/pwx7paj.css");


// Root styles
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul li {
  list-style: none;
}

a {
  text-decoration: none;
  color: var(--black);
}

// Colors
:root {
  --black: #2C363F;
  --green: #C5EFCB;
  --purple: #B161F7;
}

// Typography
html {
  font-size: 18px;
  font-family: muli, sans-serif;
  line-height: 1.3;
  color: var(--black);
}

h1,
h2 {
  font-family: gastromond, sans-serif;
}

h1 {
  font-size: 3.3em;
}

h2 {
  font-size: 2.1em;
}

p {
  opacity: .8;
}

.sub-heading {
  text-transform: uppercase;
  opacity: 1;
}

.bold-p {
  font-weight: 700;
}

.light-text {
  color: #fff;
}

.purple-heading {
  color: var(--purple);
}

// UI elements
.btn {
  padding: 16px 32px;
  font-size: 20px;
  font-weight: 700;
  border-radius: 18px;
}

.btn-primary {
  color: #fff;
  background-color: var(--green);
}

.btn-primary:hover {
  opacity: .8;
}

.btn-secondary-green {
  color: var(--green);
  border: 2px solid var(--green);
  background-color: transparent;
  font-size: 1em;
  font-weight: 400;
  transition: all .8s;
}

.btn-secondary-green:hover {
  color: #fff;
  background-color: var(--green);
}

// Containers
.container {
  max-width: 1170px;
  margin: 0 auto 13rem;
  padding: 0 1%;

}